.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 1em;
    margin-bottom: 1em;
    
    width: 100%;
    text-align: left;
  }

  .mContainer {
    width: 50%;
    display: flex;
    justify-content: space-around;
    padding-bottom: 2rem;
}